// =============================================================================
// x402jobs Onboard API
// =============================================================================
// POST /api/v1/x402jobs/onboard
// 
// One-stop onboarding service that:
// - Creates a NexFlow metered endpoint
// - Enables x402 verification
// - Configures webhooks/alerts
// - Creates a scoped API key
// - Returns complete setup information

import { NextRequest, NextResponse } from 'next/server';
import { onboardX402Job } from '@/lib/x402jobs-onboard';
import { extractRequestContext, wrapResponse, createErrorResponse } from '@/lib/request-logging';
import { z } from 'zod';

/**
 * Input schema for x402jobs onboarding
 */
const onboardInputSchema = z.object({
  targetUrl: z.string()
    .url('Must be a valid URL')
    .max(2048, 'URL must be less than 2048 characters')
    .refine((url) => url.startsWith('http://') || url.startsWith('https://'), 'URL must use http:// or https://'),
  
  name: z.string()
    .min(1, 'Name is required')
    .max(100, 'Name must be less than 100 characters')
    .optional(),
  
  price: z.string()
    .regex(/^\d+$/, 'Price must be a positive integer in atomic units')
    .optional(),
  
  recipientAddress: z.string()
    .regex(/^0x[a-fA-F0-9]{40}$/, 'Must be a valid Ethereum address')
    .optional(),
  
  webhookUrl: z.string()
    .url('Must be a valid URL')
    .optional(),
  
  webhookEvents: z.array(z.enum([
    'payment.verified',
    'payment.failed',
    'usage.recorded',
    'session.completed',
    'endpoint.created',
    'endpoint.updated',
    'endpoint.deleted'
  ])).optional(),
  
  alertThreshold: z.number()
    .positive('Alert threshold must be positive')
    .optional(),
  
  alertMetric: z.string()
    .min(1, 'Alert metric name is required')
    .optional(),
  
  apiKeyName: z.string()
    .min(1, 'API key name is required')
    .max(100, 'API key name must be less than 100 characters')
    .optional(),
  
  network: z.string()
    .default('base')
    .optional(),
  
  asset: z.string()
    .regex(/^0x[a-fA-F0-9]{40}$/, 'Asset must be a valid Ethereum address')
    .optional(),
  
  description: z.string()
    .max(500, 'Description must be less than 500 characters')
    .optional(),
}).strict();

export type OnboardInput = z.infer<typeof onboardInputSchema>;

/**
 * POST /api/v1/x402jobs/onboard
 * 
 * Onboard a new x402 job endpoint
 * 
 * Request body:
 * {
 *   "targetUrl": "https://api.example.com/resource",
 *   "name": "My API Endpoint",
 *   "price": "1000000",
 *   "recipientAddress": "0x...",
 *   "webhookUrl": "https://example.com/webhook",
 *   "webhookEvents": ["payment.verified", "usage.recorded"],
 *   "alertThreshold": 100,
 *   "alertMetric": "request.count",
 *   "apiKeyName": "My API Key",
 *   "network": "base",
 *   "asset": "0x833589fCD6eDb6E08f4c7C32D4f71b54bdA02913",
 *   "description": "Optional description"
 * }
 */
export async function POST(request: NextRequest) {
  const ctx = await extractRequestContext(request, '/api/v1/x402jobs/onboard');
  
  try {
    // Parse and validate request body
    const body = await request.json();
    const validation = onboardInputSchema.safeParse(body);
    
    if (!validation.success) {
      return NextResponse.json(
        {
          error: 'Validation error',
          code: 'VALIDATION_ERROR',
          details: validation.error.errors.map(err => ({
            field: err.path.join('.'),
            message: err.message,
            code: err.code,
          })),
        },
        { status: 400 }
      );
    }
    
    const input = validation.data;
    
    // Get admin API key from request (for creating resources)
    // In production, this might come from a service account or be handled differently
    const authHeader = request.headers.get('authorization');
    if (!authHeader || !authHeader.startsWith('Bearer ')) {
      return NextResponse.json(
        {
          error: 'Authentication required',
          code: 'AUTH_REQUIRED',
          message: 'Bearer token required in Authorization header',
        },
        { status: 401 }
      );
    }
    
    // Run onboarding process
    // TypeScript: zod validation ensures targetUrl is present
    const token = authHeader.replace('Bearer ', '');
    if (!input.targetUrl) {
      // This should never happen due to zod validation, but TypeScript needs this check
      return NextResponse.json(
        { error: 'targetUrl is required', code: 'VALIDATION_ERROR' },
        { status: 400 }
      );
    }
    const result = await onboardX402Job({ ...input, targetUrl: input.targetUrl }, token);
    
    return NextResponse.json(result, { status: 200 });
  } catch (error) {
    // Preserve the original error message for better debugging
    const errorMessage = error instanceof Error ? error.message : String(error);
    const errorCode = error instanceof Error && (error as any).code ? (error as any).code : 'ONBOARDING_ERROR';
    
    return NextResponse.json(
      {
        error: errorMessage,
        code: errorCode,
        requestId: ctx.requestId,
        // Include helpful context for admin key errors
        ...(errorMessage.includes('Admin API key') && {
          hint: 'Make sure the admin key exists in the production database and has the "admin" role. Run: npm run create:admin-key to create a new admin key in production.',
        }),
      },
      { status: 500 }
    );
  }
}

