// =============================================================================
// x402jobs Onboarding Service
// =============================================================================
// Service that handles complete onboarding of a new x402 job endpoint
// Creates endpoint, API key, webhooks, and alerts in a single transaction

import { createEndpoint, type MeteredEndpoint } from '@/db/metered-endpoints';
import { createApiKey, type ApiKeyWithToken } from '@/db/api-keys';
import { createWebhookConfig, type WebhookConfig } from '@/db/webhook-configs';
import { createAlertRule, type AlertRule } from '@/db/alert-rules';
import { findApiKeyByToken } from '@/db/api-keys';
import { createLogger } from './logger';
import crypto from 'crypto';

const logger = createLogger({ component: 'X402JobsOnboard' });

export interface OnboardInput {
  targetUrl: string;
  name?: string;
  price?: string;
  recipientAddress?: string;
  webhookUrl?: string;
  webhookEvents?: ReadonlyArray<
    'payment.verified' |
    'payment.failed' |
    'usage.recorded' |
    'session.completed' |
    'endpoint.created' |
    'endpoint.updated' |
    'endpoint.deleted'
  >;
  alertThreshold?: number;
  alertMetric?: string;
  apiKeyName?: string;
  network?: string;
  asset?: string;
  description?: string;
}

export interface OnboardResult {
  success: true;
  endpoint: {
    id: string;
    name: string;
    meteredUrl: string;
    upstreamUrl: string;
    price: string;
    network: string;
    status: string;
  };
  apiKey: {
    id: string;
    name: string;
    token: string;
    role: string;
    rateLimit: number;
  };
  webhook?: {
    id: string;
    url: string;
    events: string[];
    enabled: boolean;
  };
  alertRule?: {
    id: string;
    name: string;
    metricName: string;
    threshold: number;
    enabled: boolean;
  };
  exampleCurl: string;
  documentation: {
    endpointUrl: string;
    apiKeyHeader: string;
    paymentHeader: string;
    webhookUrl?: string;
  };
}

/**
 * Generate a default name from URL
 */
function generateDefaultName(url: string): string {
  try {
    const urlObj = new URL(url);
    const hostname = urlObj.hostname.replace(/^www\./, '');
    const path = urlObj.pathname.split('/').filter(Boolean).join('-') || 'api';
    return `${hostname}-${path}`.substring(0, 100);
  } catch {
    return `endpoint-${Date.now()}`;
  }
}

/**
 * Generate webhook secret
 */
function generateWebhookSecret(): string {
  return crypto.randomBytes(32).toString('hex');
}

/**
 * Get base URL for metered endpoints
 */
function getMeteredBaseUrl(): string {
  const baseUrl = process.env.NEXFLOW_BASE_URL || 
                  process.env.NEXT_PUBLIC_BASE_URL || 
                  process.env.VERCEL_URL ? `https://${process.env.VERCEL_URL}` : 
                  'https://nexflowapp.app';
  return baseUrl;
}

/**
 * Main onboarding function
 */
export async function onboardX402Job(
  input: OnboardInput,
  adminApiKeyToken: string
): Promise<OnboardResult> {
  logger.info({ input: { ...input, targetUrl: input.targetUrl } }, 'Starting x402jobs onboarding');
  
  // Validate admin API key
  const adminKey = await findApiKeyByToken(adminApiKeyToken);
  if (!adminKey) {
    throw new Error('Admin API key not found. The provided API key does not exist in the database.');
  }
  if (adminKey.role !== 'admin') {
    throw new Error(`Admin API key required for onboarding. Provided key has role: ${adminKey.role}`);
  }
  
  // Set defaults
  const name = input.name || generateDefaultName(input.targetUrl);
  const price = input.price || '1000000'; // 1 USDC in atomic units
  const network = input.network || 'base';
  const asset = input.asset || '0x833589fCD6eDb6E08f4c7C32D4f71b54bdA02913'; // USDC on Base
  const recipientAddress = input.recipientAddress || process.env.X402_RECIPIENT_ADDRESS;
  
  if (!recipientAddress) {
    throw new Error('recipientAddress is required (either in input or X402_RECIPIENT_ADDRESS env var)');
  }
  
  // Validate recipient is not USDC contract
  const USDC_CONTRACT = '0x833589fCD6eDb6E08f4c7C32D4f71b54bdA02913';
  if (recipientAddress.toLowerCase() === USDC_CONTRACT.toLowerCase()) {
    throw new Error('recipientAddress cannot be the USDC contract address');
  }
  
  // Determine chain ID from network
  const chainId = network === 'base' ? 8453 : 
                  network === 'ethereum' ? 1 :
                  network === 'polygon' ? 137 :
                  network === 'arbitrum' ? 42161 :
                  network === 'optimism' ? 10 : 8453;
  
  const tokenSymbol = 'USDC';
  
  // 1. Create metered endpoint
  logger.info({ name, targetUrl: input.targetUrl }, 'Creating metered endpoint');
  const endpoint = await createEndpoint({
    name,
    description: input.description,
    upstreamUrl: input.targetUrl,
    price,
    network,
    tokenAddress: asset,
    tokenSymbol,
    chainId,
    recipientAddress,
    status: 'active',
    createdBy: adminKey.id,
  });
  
  logger.info({ endpointId: endpoint.id }, 'Endpoint created');
  
  // 2. Create scoped API key
  const apiKeyName = input.apiKeyName || `${name} API Key`;
  logger.info({ apiKeyName }, 'Creating API key');
  const apiKey = await createApiKey({
    name: apiKeyName,
    role: 'user',
    userId: adminKey.userId,
    rateLimit: 1000,
  });
  
  logger.info({ apiKeyId: apiKey.id }, 'API key created');
  
  // 3. Create webhook if URL provided
  let webhook: WebhookConfig | undefined;
  if (input.webhookUrl) {
    logger.info({ webhookUrl: input.webhookUrl }, 'Creating webhook');
    const webhookEvents = input.webhookEvents || [
      'payment.verified',
      'payment.failed',
      'usage.recorded',
    ];
    
    webhook = await createWebhookConfig({
      apiKeyId: apiKey.id,
      endpointId: endpoint.id,
      url: input.webhookUrl,
      secret: generateWebhookSecret(),
      events: webhookEvents,
      enabled: true,
    });
    
    logger.info({ webhookId: webhook.id }, 'Webhook created');
  }
  
  // 4. Create alert rule if threshold provided
  let alertRule: AlertRule | undefined;
  if (input.alertThreshold && input.alertMetric) {
    logger.info({ metric: input.alertMetric, threshold: input.alertThreshold }, 'Creating alert rule');
    alertRule = await createAlertRule({
      name: `${name} - ${input.alertMetric} Alert`,
      metricName: input.alertMetric,
      condition: 'gt',
      threshold: input.alertThreshold,
      windowMs: 60000, // 1 minute window
      tags: {
        endpointId: endpoint.id,
      },
      enabled: true,
      severity: 'high',
      notificationChannels: [],
    });
    
    logger.info({ alertRuleId: alertRule.id }, 'Alert rule created');
  }
  
  // 5. Build response
  const baseUrl = getMeteredBaseUrl();
  const meteredUrl = `${baseUrl}/api/v1/metered/${endpoint.id}`;
  
  const exampleCurl = `curl -X POST ${meteredUrl} \\
  -H "Authorization: Bearer ${apiKey.token}" \\
  -H "Content-Type: application/json" \\
  -H "x-payment: x402 <your_payment_header>" \\
  -d '{"your": "request", "body": "here"}'`;
  
  const result: OnboardResult = {
    success: true,
    endpoint: {
      id: endpoint.id,
      name: endpoint.name,
      meteredUrl,
      upstreamUrl: endpoint.upstreamUrl,
      price: endpoint.price,
      network: endpoint.network,
      status: endpoint.status,
    },
    apiKey: {
      id: apiKey.id,
      name: apiKey.name,
      token: apiKey.token,
      role: apiKey.role,
      rateLimit: apiKey.rateLimit,
    },
    ...(webhook && {
      webhook: {
        id: webhook.id,
        url: webhook.url,
        events: webhook.events,
        enabled: webhook.enabled,
      },
    }),
    ...(alertRule && {
      alertRule: {
        id: alertRule.id,
        name: alertRule.name,
        metricName: alertRule.metricName,
        threshold: alertRule.threshold,
        enabled: alertRule.enabled,
      },
    }),
    exampleCurl,
    documentation: {
      endpointUrl: meteredUrl,
      apiKeyHeader: `Authorization: Bearer ${apiKey.token}`,
      paymentHeader: 'x-payment: x402 <your_payment_header>',
      ...(webhook && { webhookUrl: webhook.url }),
    },
  };
  
  logger.info({ 
    endpointId: endpoint.id, 
    apiKeyId: apiKey.id,
    webhookId: webhook?.id,
    alertRuleId: alertRule?.id,
  }, 'Onboarding completed successfully');
  
  return result;
}

